<template>
  <div
    style="width: 200px;"
    class="d-flex justify-center align-center"
  >
    <v-badge
      color="red"
      inline
      tile
      :content="selected.length"
      :value="selected.length"
    >
      <template #badge>
        {{ selected.length }}
      </template>
      {{ title }}
    </v-badge>
    <v-tooltip
      v-if="selected.length"
      bottom
    >
      <template v-slot:activator="{ on }">
        <v-btn
          x-small
          icon
          color="grey"
          v-on="on"
          @click.stop.prevent="clearItems"
        >
          <v-icon :size="12">
            fas fa-times
          </v-icon>
        </v-btn>
      </template>
      <span>{{ $t('common.clearSelected') }}</span>
    </v-tooltip>
  </div>
</template>

<script>
export default {
  name: 'TiktokAdsmanagerTabsBadge',

  props: {
    tabName: {
      type: String,
      required: true
    },
    currentTabNumber: {
      type: Number,
      required: true
    },
    selected: {
      type: Array,
      required: true
    },
  },

  computed: {
    title() {
      switch (this.tabName) {
      case 'ad-accounts':
        return this.$t('adsmanager.adAccounts.adAccounts');
      case 'campaigns':
        return this.$t('adsmanager.tabs.campaigns');
      case 'adsets':
        return this.$t('common.adsets');
      case 'ads':
        return this.$t('adsmanager.tabs.ads');
      }

      return this.$t('common.accounts');
    },
  },

  methods: {
    async refreshCurrentList() {
      switch (this.currentTabNumber) {
      case 1:
        await this.$store.dispatch('tiktokAdsmanagerAdAccounts/loadMainTableData', { endpoint: 'tiktok/adaccounts' });
        break;
      case 2:
        await this.$store.dispatch('tiktokAdsmanagerCampaigns/loadMainTableData', { endpoint: 'tiktok/campaigns' });
        break;
      case 3:
        await this.$store.dispatch('tiktokAdsmanagerAdsets/loadMainTableData', { endpoint: 'tiktok/adsets' });
        break;
      case 4:
        await this.$store.dispatch('tiktokAdsmanagerAds/loadMainTableData', { endpoint: 'tiktok/creatives' });
        break;
      }
    },

    clearItems() {
      switch (this.tabName) {
      case 'ad-accounts':
        return this.clearAdAccounts();
      case 'campaigns':
        return this.clearCampaigns();
      case 'adsets':
        return this.clearAdsets();
      case 'ads':
        return this.clearAds();
      }

      return this.clearAccounts();
    },

    async clearAccounts() {
      await this.$store.dispatch('tiktokAdsmanagerAccounts/clearSelected');
      this.refreshCurrentList();
    },

    async clearAdAccounts() {
      await this.$store.dispatch('tiktokAdsmanagerAdAccounts/clearSelected');
      if (this.currentTabNumber > 1) {
        this.refreshCurrentList();
      }
    },

    async clearCampaigns() {
      await this.$store.dispatch('tiktokAdsmanagerCampaigns/clearSelected');
      if (this.currentTabNumber > 2) {
        this.refreshCurrentList();
      }
    },

    async clearAdsets() {
      await this.$store.dispatch('tiktokAdsmanagerAdsets/clearSelected');
      if (this.currentTabNumber > 3) {
        this.refreshCurrentList();
      }
    },

    async clearAds() {
      await this.$store.dispatch('tiktokAdsmanagerAds/clearSelected');
    }
  }
};
</script>