<template>
  <div>
    <tabs />
    <component :is="componentName" />
  </div>
</template>

<script>
import Tabs       from '@/components/tiktok/adsmanager/TiktokAdsmanagerTabs.vue';

import Accounts   from './TiktokAdsManagerAccounts.vue';
import AdAccounts from './TiktokAdsManagerAdAccounts.vue';
import Ads        from './TiktokAdsManagerAds.vue';
import Adsets     from './TiktokAdsManagerAdsets.vue';
import Campaigns  from './TiktokAdsManagerCampaigns.vue';

export default {
  name: 'TiktokAdsManager',

  components: {
    Tabs,
    Accounts,
    AdAccounts,
    Adsets,
    Ads,
    Campaigns,
  },

  computed: {
    componentName() {
      if (typeof this.$route.params.tab !== 'undefined') {
        return this.$route.params.tab;
      }
      return 'accounts';
    }
  }
};
</script>