<template>
  <v-dialog
    :value="dialogs.bulkChangeBudget"
    width="500"
    :transition="false"
    scrollable
    @click:outside="closeDialog"
    @keydown.esc="closeDialog"
  >
    <v-card>
      <v-toolbar
        :height="48"
        dark
        color="primary"
        class="mb-5"
        flat
      >
        <v-toolbar-title>
          {{ $t('common.changeBudget') }}
        </v-toolbar-title>

        <v-spacer />

        <v-toolbar-items>
          <v-btn
            icon
            @click="closeDialog"
          >
            <v-icon>
              fas fa-times
            </v-icon>
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>

      <v-card-text>
        <v-text-field
          v-model="budget"
          suffix="USD"
          type="number"
          min="0"
          outlined
          dense
          hide-details="auto"
          :error-messages="budgetErrors"
          @blur="blurBudget"
        />
      </v-card-text>

      <v-divider />

      <v-card-actions>
        <v-spacer />
        <v-btn
          text
          color="grey"
          @click="closeDialog"
        >
          {{ $t('common.close') }}
        </v-btn>
        <v-btn
          text
          color="primary"
          :loading="loading"
          @click="touch"
        >
          {{ $t('common.update') }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { required, minValue } from 'vuelidate/lib/validators';
import { mapGetters }         from 'vuex';

export default {
  name: 'TiktokAdsmanagerCampaignsDialogsBulkChangeBudget',

  data() {
    return {
      loading: false,
      budget: 0,
      touched: false,
    };
  },

  validations: {
    budget: { required, minValue: minValue(50) },
  },

  computed: {
    ...mapGetters({
      dialogs: 'tiktokAdsmanagerCampaigns/dialogs',
      selected: 'tiktokAdsmanagerCampaigns/selected',
    }),

    budgetErrors() {
      if (this.touched && this.$v.budget.$invalid) {
        return this.$t('validation.min', { length: 50 }); 
      }
      return null;
    },
  },

  watch: {
    budget() { this.touched = false; },
  },

  methods: {
    closeDialog() {
      this.$store.dispatch('tiktokAdsmanagerCampaigns/closeDialog', 'bulkChangeBudget');
    },

    touch() {
      this.$v.$touch();

      if (this.$v.$invalid) {
        this.touched = true;
      } else {
        this.bulkChangeBudget();
      }
    },

    async bulkChangeBudget() {
      try {
        this.loading = true;
    
        const ids = this.selected.map(i => i.id);
        const budget = this.budget;
        const response = await this.api.patch('/tiktok/campaigns/budget-change', { ids, budget });

        if (response.status === 200) {
          await this.$store.dispatch('tiktokAdsmanagerCampaigns/loadMainTableData', { endpoint: 'tiktok/campaigns' });
          await this.$store.dispatch('main/alert', {
            color: 'success',
            message: this.$t('common.success')
          });
          this.loading = false;
          await this.$store.dispatch('tiktokAdsmanagerCampaigns/clearSelected');
          this.closeDialog();
        } else {
          throw new Error('status not 200');
        }
      } catch (error) {
        this.loading = false;
        this.$store.dispatch('main/alert', {
          color: 'error',
          message: this.$t('common.somethingWentWrong')
        });
      }
    },

    blurBudget() {
      if (!this.budget || this.budget < 0) {
        this.budget = 0;
      }
    },
  }
};
</script>
