export default {
  methods: {
    statCellData(item, aggregateColumn) {
      return item.stats[aggregateColumn] ? item.stats[aggregateColumn] : 0;
    },

    selectItem(item, value) {
      if (value) {
        this.selected.push(item);
      } else {
        this.selected = this.selected.filter(i => i.id !== item.id);
      }
    },

    selectAll(value) {
      if (value) {
        this.selected = this.mainTableData;
      } else {
        this.selected = [];
      }
    }
  }
};