<template>
  <td class="tiktok-ads-status">
    <v-chip
      :color="getStatusChipColor(item.status)"
      small
      class="ml-1"
      label
      outlined
    >
      {{ item.status.toString().toUpperCase() }}
    </v-chip>

    <div>
      <v-tooltip
        v-if="hasReason"
        bottom
        max-width="300"
      >
        <template v-slot:activator="{ on }">
          <v-chip
            color="red"
            x-small
            class="ml-1 my-2"
            label
            outlined
            v-on="on"
          >
            {{ hasReason.reason.title }}
          </v-chip>
        </template>
        <span>{{ hasReason.reason.reasons }}</span>
      </v-tooltip>
    </div>
    <div>
      <v-tooltip
          v-if="isModerationRejected"
          bottom
          max-width="300"
      >
        <template v-slot:activator="{ on }">
          <v-chip
              color="red"
              x-small
              class="ml-1 my-2 rejected-block"
              label
              outlined
              v-on="on"
              @click="handleRejection"
          >
            <span>REJECTED</span>
            <v-icon
                class="question-icon"
                :size="14"
                color="red"
            >
              far fa-question-circle
            </v-icon>
          </v-chip>
        </template>
        <span>{{ $t('adsmanager.adsets.rejectedQuestionMark')}}</span>
      </v-tooltip>
    </div>
  </td>
</template>
<script>
import getStatusChipColor from '@/helpers/tiktok/mainTable/status';
import { mapGetters } from 'vuex';

export default {
  name: 'TiktokAdsmanagerAdsMainTableStatus',

  props: {
    item: {
      type: Object,
      required: true
    },
  },

  data() {
    return {
      getStatusChipColor
    };
  },

  computed:{
    ...mapGetters({
      adsReasons: 'tiktokAdsmanagerAds/adsReasons',
    }),

    hasReason() {
      const itemReason = this.adsReasons.filter(reason => reason.id === this.item.id);

      return itemReason.length ? itemReason[0] : '';
    },

    isModerationRejected() {
      return !this.hasReason && (this.item.ad_audit_result_type === 2 || this.item.ad_audit_result_type === 3);
    }
  },

  methods:{
    getReasonChipColor(value) {
      switch(value.toString()) {
      case '3':
        return 'red';
      case '2':
        return 'orange';
      }
    },

    handleRejection() {
      this.$store.dispatch('tiktokAdsmanagerAds/handleAdsRejectionReason', this.item.id);
    }
  }
};
</script>

<style>
.tiktok-ads-status .rejected-block {
  padding: 10px;
  cursor:pointer;
}
.tiktok-ads-status .question-icon {
  margin-left: 4px;
}
</style>