<template>
  <td>
    <span
      class="span-link"
      @click="editAccount(item)"
    >{{ item.name }}</span>
  </td>
</template>
<script>
export default {
  name: 'TiktokAdsmanagerAccountsMainTableName',

  props: {
    item: {
      type: Object,
      required: true
    },
  },

  methods: {
    async editAccount(data) {
      await this.$store.dispatch('tiktokAdsmanagerAccounts/setSingleAccountData', data);
      this.$store.dispatch('tiktokAdsmanagerAccounts/openDialog', 'edit');
    },
  }
};
</script>