<template>
  <div style="padding: 8px;">
    <dialogs-bulk-delete v-if="dialogs.bulkDelete" />
    <dialogs-bulk-duplicate v-if="dialogs.bulkDuplicate" />
    <dialogs-bulk-change-budget v-if="dialogs.bulkChangeBudget" />
    <topbar />
    <main-table />
  </div>
</template>

<script>
import Topbar               from '@/components/tiktok/adsmanager/adsets/TiktokAdsmanagerAdsetsTopbar.vue';
import DialogsBulkChangeBudget from  '@/components/tiktok/adsmanager/adsets/dialogs/TiktokAdsmanagerAdsetsDialogsBulkChangeBudget.vue';
import DialogsBulkDelete    from '@/components/tiktok/adsmanager/adsets/dialogs/TiktokAdsmanagerAdsetsDialogsBulkDelete.vue';
import DialogsBulkDuplicate from '@/components/tiktok/adsmanager/adsets/dialogs/TiktokAdsmanagerAdsetsDialogsBulkDuplicate.vue';
import MainTable            from '@/components/tiktok/adsmanager/adsets/mainTable/TiktokAdsmanagerAdsetsMainTable.vue';
import { mapGetters }       from 'vuex';

export default {
  name: 'TiktokAdsManagerAdsets',

  components: {
    MainTable,
    Topbar,
    DialogsBulkDelete,
    DialogsBulkDuplicate,
    DialogsBulkChangeBudget,

  },

  computed: {
    ...mapGetters({
      dialogs: 'tiktokAdsmanagerAdsets/dialogs',
    }),
  },

  created() {
    this.$store.dispatch('tiktokAdsmanagerAdsets/loadMainTableData', { endpoint: 'tiktok/adsets' });
  }
};
</script>


