<template>
  <v-dialog
    :value="dialogs.createPixel"
    width="950"
    :transition="false"
    scrollable
    @click:outside="closeDialog"
    @keydown.esc="closeDialog"
  >
    <!-- ДИАЛОГ ОШИБКИ -->
    <v-dialog
      v-model="detailsDialog"
      width="700"
      scrollable
    >
      <v-card>
        <v-card-title class="headline">
          {{ $t('common.learnMore') }}
        </v-card-title>

        <v-card-text style="margin-top: 40px;">
          <div class="d-flex justify-end">
            <div style="position: fixed; margin-top: -40px;">
              <v-btn
                color="primary"
                text
                @click="copyToClipboard(JSON.stringify(forDetails))"
              >
                <v-icon
                  :size="14"
                  class="mr-2"
                >
                  fas fa-copy
                </v-icon>
                {{ $t('common.copy') }}
              </v-btn>
            </div>
          </div>
          <code class="mt-0">{{ JSON.stringify(forDetails, null, 2) }}</code>
        </v-card-text>

        <v-divider />

        <v-card-actions>
          <v-spacer />
          <v-btn
            color="primary"
            text
            @click="detailsDialog = false"
          >
            {{ $t('common.close') }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- ДИАЛОГ ОШИБКИ КОНЕЦ -->

    <v-card>
      <v-toolbar
        :height="48"
        dark
        color="primary"
        class="mb-5"
        flat
      >
        <v-toolbar-title>
          {{ $t('adsmanager.cabs.actions.createPixel') }}
        </v-toolbar-title>

        <v-spacer />

        <v-toolbar-items>
          <v-btn
            icon
            @click="closeDialog"
          >
            <v-icon>
              fas fa-times
            </v-icon>
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>

      <v-card-text>
        <v-data-table
          :headers="headers"
          :items="selected"
        >
          <!-- КАБИНЕТ -->
          <template #[`item.adAccount`]="{ item }">
            {{ item.name }}
          </template>
          <!-- КАБИНЕТ КОНЕЦ -->

          <!-- НАЗВАНИЕ ПИКСЕЛЯ -->
          <template #[`item.pixelName`]="{ item }">
            <v-text-field
              v-if="items[item.id]"
              v-model="items[item.id].pixelName"
              :label="$t('adsmanager.adAccounts.pixelName')"
              outlined
              :readonly="readonly"
              dense
              hide-details="auto"
              class="px-2"
              :class="{'pt-2': pixelNameErrorMessage(item.id)}"
              :error-messages="pixelNameErrorMessage(item.id)"
            />
          </template>
          <!-- НАЗВАНИЕ ПИКСЕЛЯ КОНЕЦ -->

          <!-- СОБЫТИЕ -->
          <template #[`item.eventType`]="{ item }">
            <v-select
              v-if="items[item.id]"
              v-model="items[item.id].eventType"
              :label="$t('common.pixelEvent')"
              :items="pixelEvents"
              outlined
              :readonly="readonly"
              dense
              hide-details
              class="px-2"
            />
          </template>
          <!-- СОБЫТИЕ КОНЕЦ -->

          <!-- URL CONTAINS -->
          <template #[`item.urlContains`]="{ item }">
            <v-text-field
              v-if="items[item.id]"
              v-model="items[item.id].urlContains"
              label="URL contains"
              outlined
              :readonly="readonly"
              dense
              hide-details="auto"
              class="px-2"
              :class="{'pt-2': emptyUrlContains.includes(item.id)}"
              :error-messages="emptyUrlContains.includes(item.id) ? $t('common.required') : null"
            />
          </template>
          <!-- URL CONTAINS КОНЕЦ -->

          <!-- СТАТУС -->
          <template #[`item.status`]="{ item }">
            <div class="px-2">
              <!-- ПРОГРЕСС -->
              <v-progress-linear
                v-if="showItemProgress(item.id)"
                :indeterminate="itemProgressLoading(item.id)"
                :value="0"
              />
              <!-- ПРОГРЕСС КОНЕЦ -->

              <!-- УСПЕХ -->
              <v-icon
                v-else-if="showItemSuccess(item.id)" 
                color="green"
              >
                check_box
              </v-icon>
              <!-- УСПЕХ КОНЕЦ -->

              <!-- НЕУСПЕХ -->
              <div
                v-else
                class="d-flex align-center"
              >
                <v-icon
                  color="red"
                  class="mr-1"
                >
                  cancel
                </v-icon>
                <span
                  class="span-link"
                  @click="openDetails(item.id)"
                >{{ $t('common.learnMore') }}</span>
              </div>
              <!-- НЕУСПЕХ КОНЕЦ -->
            </div>
          </template>
          <!-- СТАТУС КОНЕЦ -->
        </v-data-table>
      </v-card-text>

      <v-divider />

      <v-card-actions>
        <v-spacer />
        <v-btn
          text
          color="grey"
          @click="closeDialog"
        >
          {{ $t('common.close') }}
        </v-btn>
        <v-btn
          text
          color="primary"
          :loading="loading"
          @click="createPixel"
        >
          {{ $t('common.create') }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import clipboard      from '@/mixins/clipboard';
import axios          from 'axios';
import { mapGetters } from 'vuex';

export default {
  name: 'TiktokAdsmanagerAdAccountsDialogsCreatePixel',

  mixins: [clipboard],

  data() {
    return {
      readonly: false,
      loading: false,
      pixelEvents: [
        { text: 'Add Payment Info', value: 'add_billing'},
        { text: 'Add to Cart', value: 'on_web_cart'},
        { text: 'Add to Wishlist', value: 'on_web_add_to_wishlist'},
        { text: 'Click Button', value: 'button'},
        { text: 'Complete Payment', value: 'shopping'},
        { text: 'Complete Registration', value: 'on_web_register'},
        { text: 'Contact', value: 'consult'},
        { text: 'Download', value: 'download_start'},
        { text: 'Initiate Checkout', value: 'initiate_order'},
        { text: 'Search', value: 'on_web_search'},
        { text: 'Submit Form', value: 'form'},
        { text: 'Subscribe', value: 'on_web_subscribe'},
        { text: 'View Content', value: 'on_web_detail'},
        { text: 'Place an Order', value: 'on_web_order'},
      ],
      items: {},
      detailsDialog: false,
      forDetails: null,
      emptyUrlContains: [],
      nameAlreadyTaken: [],
      emptyPixelNames: [],
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'tiktokAdsmanagerAdAccounts/dialogs',
      selected: 'tiktokAdsmanagerAdAccounts/selected',
    }),

    headers() {
      return [
        { text: this.$t('common.adAccount'), value: 'adAccount', sortable: false },
        { text: '', value: 'pixelName', sortable: false, width: 200, },
        { text: '', value: 'eventType', sortable: false, width: 200, },
        { text: '', value: 'urlContains', sortable: false, width: 200, },
        { text: this.$t('common.status'), value: 'status', sortable: false, width: 200, }
      ];
    },
  },

  watch: {
    items: {
      deep: true,
      handler() {
        if (this.emptyUrlContains.length) {
          this.emptyUrlContains = [];
        }
        if (this.nameAlreadyTaken.length) {
          this.nameAlreadyTaken = [];
        }
        if (this.emptyPixelNames.length) {
          this.emptyPixelNames = [];
        }
      }
    },
  },

  created() {    
    for (const adAccount of this.selected) {
      this.$set(this.items, adAccount.id, {
        pixelName: '',
        eventType: 'on_web_subscribe',
        urlContains: '',
        response: {
          loading: false,
          success: null,
          errorMessage: null
        },
        pixels: adAccount.pixels,
      });
    }
  },

  methods: {
    closeDialog() {
      this.$store.dispatch('tiktokAdsmanagerAdAccounts/closeDialog', 'createPixel');
    },

    responseExists(id) {
      return this.items[id] && this.items[id].response;
    },

    showItemProgress(id) {
      return this.responseExists(id) && this.items[id].response.success === null;
    },

    itemProgressLoading(id) {
      return this.responseExists(id) && this.items[id].response.loading;
    },

    showItemSuccess(id) {
      return this.responseExists(id) && this.items[id].response.success;
    },

    pixelNameErrorMessage(id) {
      if (this.emptyPixelNames.includes(id)) {
        return this.$t('common.required');
      }
      if (this.nameAlreadyTaken.includes(id)) {
        return this.$t('validation.nameHasBeenAlreadyTaken');
      }

      return null;
    },

    validate() {
      this.emptyUrlContains = [];
      this.nameAlreadyTaken = [];
      this.emptyPixelNames = [];

      for (const id in this.items) {
        if (!this.items[id].urlContains) {
          this.emptyUrlContains.push(id);
        }
        if (!this.items[id].pixelName) {
          this.emptyPixelNames.push(id);
        } else if (Array.isArray(this.items[id].pixels) && this.items[id].pixels.length) {
          for (const pixel of this.items[id].pixels) {
            if (pixel.name === this.items[id].pixelName) {
              this.nameAlreadyTaken.push(id);
            }
          }
        }
      }
      
      return !this.emptyUrlContains.length &&
        !this.nameAlreadyTaken.length &&
        !this.emptyPixelNames.length;
    },

    async createPixel() {
      if (!this.validate()) return;

      try {
        this.loading = true;
        const items = [];
        
        for (const id in this.items) {
          const { pixelName: name, eventType, urlContains } = this.items[id];
          this.setNewRequestParams(this.items[id]);
          items.push({ id, name, eventType, urlContains });
        }
        
        await axios.all(items.map((item) => {
          return this.api.post('/tiktok/user-pixels', item)
            .then(() => {
              this.readonly = true;
              this.items[item.id].response.success = true;
            })
            .catch((e) => {
              this.items[item.id].response.success = false;
              this.items[item.id].response.errorMessage = e;
            })
            .finally(() => {
              this.items[item.id].response.loading = false;
            });
        }));
      } catch (error) {
        // 
      } finally {
        await this.$store.dispatch('tiktokAdsmanagerAdAccounts/loadMainTableData', { endpoint: 'tiktok/adaccounts' });
        await this.$store.dispatch('tiktokAdsmanagerAdAccounts/replaceSelected', {data: this.items});
        this.loading = false;
        this.replacePixels();
      }
    },

    setNewRequestParams(item) {
      item.response = {
        loading: true,
        success: null,
        errorMessage: null
      };
    },

    getData(itemId, item) {
      return {
        adAccountId: itemId,
        name: item.pixelName,
        eventType: item.eventType,
        urlContains: item.urlContains,
      };
    },

    openDetails(id) {
      this.forDetails = this.items[id].response.errorMessage;
      this.detailsDialog = true;
    },

    replacePixels() {
      for (const adAccount of this.selected) {
        this.$set(this.items[adAccount.id], 'pixels', adAccount.pixels);
      }
    }
  }
};
</script>
