<template>
  <div style="padding: 8px;">
    <create-dialog v-if="dialogs.create" />
    <edit-dialog v-if="dialogs.edit" />
    <dialogs-bulk-delete-confirm v-if="dialogs.bulkDeleteConfirm" />
    <topbar />
    <main-table />
  </div>
</template>

<script>
import Topbar                          from '@/components/tiktok/adsmanager/accounts/TiktokAdsmanagerAccountsTopbar.vue';
import CreateDialog                    from '@/components/tiktok/adsmanager/accounts/create/TiktokAdsmanagerCreateDialog.vue';
import DialogsBulkDeleteConfirm        from '@/components/tiktok/adsmanager/accounts/dialogs/TiktokAdsmanagerAccountsDialogsBulkDeleteConfirm.vue';
import EditDialog                      from '@/components/tiktok/adsmanager/accounts/edit/TiktokAdsmanagerEditDialog.vue';
import MainTable                       from '@/components/tiktok/adsmanager/accounts/mainTable/TiktokAdsmanagerAccountsMainTable.vue';
import { tiktokAccessTokenValidation } from '@/helpers/validation';
import md5                             from 'md5';
import { mapGetters }                  from 'vuex';

export default {
  name: 'TiktokAdsManagerAccounts',

  components: {
    MainTable,
    Topbar,
    CreateDialog,
    EditDialog,
    DialogsBulkDeleteConfirm
  },

  data() {
    return {
      updateInterval: null,
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'tiktokAdsmanagerAccounts/dialogs',
    }),
  },

  created() {
    this.$store.dispatch('tiktokAdsmanagerAccounts/loadMainTableData', { endpoint: 'tiktok/accounts' });
  },

  destroyed() {
    clearInterval(this.updateInterval);
  },

  methods: {
    async readClipboard() {
      if (!document.hasFocus()) return;
      for (const value of Object.values(this.dialogs)) {
        if (value) return;
      }
      
      try {
        const data = await navigator.clipboard.readText();
        const hashedAccessToken = md5(data);
        const storedAccessToken = localStorage.getItem('hashedAccountAccessToken');
          
        if (
          tiktokAccessTokenValidation(data) &&
          (storedAccessToken === null || storedAccessToken !== hashedAccessToken)  
        ) {
          await this.$store.dispatch('tiktokAdsmanagerAccounts/setSingleAccountData', { access_token: data });
          this.$store.dispatch('tiktokAdsmanagerAccounts/openDialog', 'create');

          localStorage.setItem('hashedAccountAccessToken', hashedAccessToken);
        }
      } catch (error) {
        console.log(error);
      }
    }
  }
};
</script>
