<template>
  <div class="d-flex align-center justify-space-between mb-2">
    <!-- ЛЕВАЯ ЧАСТЬ -->
    <div>
      <!-- ЗАПУСТИТЬ -->
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="selected.length === 0"
            color="primary"
            icon
            :value="selected"
            :loading="loading.bulkRun"
            @click="bulkRun"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-play
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.run') }}</span>
      </v-tooltip>
      <!-- ЗАПУСТИТЬ КОНЕЦ -->

      <!-- ОСТАНОВИТЬ -->
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="selected.length === 0"
            color="primary"
            icon
            :value="selected"
            :loading="loading.bulkPause"
            @click="bulkPause"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-stop
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.toPause') }}</span>
      </v-tooltip>
      <!-- ОСТАНОВИТЬ КОНЕЦ -->

      <!-- ДУБЛИРОВАТЬ -->
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="selected.length === 0"
            color="primary"
            icon
            :value="selected"
            @click="bulkDuplicate"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-clone
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.duplicate') }}</span>
      </v-tooltip>
      <!-- ДУБЛИРОВАТЬ КОНЕЦ -->

      <!-- УДАЛИТЬ -->
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="selected.length === 0"
            color="primary"
            icon
            :value="selected"
            @click="bulkDelete"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-trash-alt
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.delete') }}</span>
      </v-tooltip>
      <!-- УДАЛИТЬ КОНЕЦ -->
    </div>
    <!-- ЛЕВАЯ ЧАСТЬ КОНЕЦ -->

    <!-- ПРАВАЯ ЧАСТЬ -->
    <div class="d-flex align-center ">
      <!-- ДАТЫ СТАТИСТИКИ -->
      <common-stat-date />
      <!-- ДАТЫ СТАТИСТИКИ КОНЕЦ -->

      <!-- ПОИСК ПО НАЗВАНИЮ -->
      <v-text-field
        dense
        clearable
        outlined
        :label="$t('filters.searchByNameOrID')"
        single-line
        prepend-inner-icon="fas fa-search"
        hide-details
        :value="searchText"
        @input="filterName"
      />
      <!-- ПОИСК ПО НАЗВАНИЮ КОНЕЦ -->
    </div>
    <!-- ПРАВАЯ ЧАСТЬ КОНЕЦ -->
  </div>
</template>

<script>
import CommonStatDate from '@/components/tiktok/adsmanager/common/TiktokAdsmanagerCommonStatDate.vue';
import { mapGetters } from 'vuex';

export default {
  name: 'TiktokAdsmanagerAdsTopbar',

  components: {
    CommonStatDate,
  },

  data() {
    return {
      searchText: '',
      loading: {
        bulkRun: false,
        bulkPause: false,
      },
    };
  },

  computed: {
    ...mapGetters({
      selected: 'tiktokAdsmanagerAds/selected',
    }),
  },

  methods: {
    filterName(search) {
      this.searchText = search;
      setTimeout(async () => {
        if (search === this.searchText) {
          await this.$store.dispatch('tiktokAdsmanagerAds/loadMainTableData', {
            endpoint: 'tiktok/creatives',
            query: search
          });
        }
      }, 500);
    },

    async bulkDelete() {
      this.$store.dispatch('tiktokAdsmanagerAds/openDialog', 'bulkDelete');
    },
    
    async bulkRun() {
      try {
        this.loading.bulkRun = true;
    
        const ids = this.selected.map(i => i.id);
        const response = await this.api.patch('/tiktok/creatives/bulk-enable', { ids });

        if (response.status === 200) {
          await this.$store.dispatch('main/alert', {
            color: 'success',
            message: this.$t('common.success')
          });
          await this.$store.dispatch('tiktokAdsmanagerAds/clearSelected');
        } else {
          throw new Error('status not 200');
        }
      } catch (error) {
        this.$store.dispatch('main/alert', {
          color: 'error',
          message: this.$t('common.somethingWentWrong')
        });
      } finally {
        this.loading.bulkRun = false;
      }
    },
    
    async bulkPause() {
      try {
        this.loading.bulkPause = true;
    
        const ids = this.selected.map(i => i.id);
        const response = await this.api.patch('/tiktok/creatives/bulk-pause', { ids });

        if (response.status === 200) {
          await this.$store.dispatch('main/alert', {
            color: 'success',
            message: this.$t('common.success')
          });
          await this.$store.dispatch('tiktokAdsmanagerAds/clearSelected');
        } else {
          throw new Error('status not 200');
        }
      } catch (error) {
        this.$store.dispatch('main/alert', {
          color: 'error',
          message: this.$t('common.somethingWentWrong')
        });
      } finally {
        this.loading.bulkPause = false;
      }
    },

    async bulkDuplicate() {
      this.$store.dispatch('tiktokAdsmanagerAds/openDialog', 'bulkDuplicate');
    }
  }
};
</script>
