<template>
  <div class="d-flex align-center justify-space-between mb-2">
    <!-- ЛЕВАЯ СТОРОНА -->
    <div>
      <!-- УДАЛИТЬ -->
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="selected.length === 0"
            color="primary"
            icon
            :value="selected"
            :loading="loading"
            @click="bulkDelete"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-trash-alt
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.delete') }}</span>
      </v-tooltip>

      <!-- КНОПКИ ДЕЙСТВИЙ - СИХНРОНИЗАЦИЯ -->
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="selected.length === 0"
            color="primary"
            icon
            :loading="loading"
            @click="sync"
            v-on="on"
          >
            <v-icon :size="16">
              sync
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.refresh') }}</span>
      </v-tooltip>
      <!-- КНОПКИ ДЕЙСТВИЙ - СИХНРОНИЗАЦИЯ КОНЕЦ -->

      <!-- МАССОВЫЕ ДЕЙСТВИЯ - ПРОВЕРИТЬ ТОКЕН -->
      <div
        v-if="!$vuetify.breakpoint.xs && !$vuetify.breakpoint.sm"
        style="float: left;"
      >
        <tik-tok-accounts-multiple-tokens-check />
      </div>
      <!-- МАССОВЫЕ ДЕЙСТВИЯ - ПРОВЕРИТЬ ТОКЕН КОНЕЦ -->
    </div>
    <!-- ЛЕВАЯ СТОРОНА КОНЕЦ -->

    <!-- ПРАВАЯ СТОРОНА -->
    <div class="d-flex align-center">
      <!-- ДАТЫ СТАТИСТИКИ -->
      <common-stat-date />
      <!-- ДАТЫ СТАТИСТИКИ КОНЕЦ -->
      
      <!-- КНОПКИ ДЕЙСТВИЙ - СОЗДАТЬ АККАУНТ -->
      <v-btn
        small
        color="primary"
        text
        style="min-width: 0;"
        class="mr-2"
        @click="addAccount"
      >
        <v-icon :size="18">
          add
        </v-icon>

        {{ $t('common.add') }} {{ $t('common.account') }}
      </v-btn>
      <!-- КНОПКИ ДЕЙСТВИЙ - СОЗДАТЬ АККАУНТ КОНЕЦ -->

      <!-- ПОИСК ПО НАЗВАНИЮ -->
      <v-text-field
        dense
        clearable
        outlined
        :label="$t('filters.searchByName')"
        single-line
        prepend-inner-icon="fas fa-search"
        hide-details
        :value="nameSearchText"
        @input="filterName"
      />
    </div>
    <!-- ПРАВАЯ СТОРОНА КОНЕЦ -->
  </div>
</template>

<script>
import CommonStatDate                    from '@/components/tiktok/adsmanager/common/TiktokAdsmanagerCommonStatDate.vue';
import moment                            from 'moment';
import { mapGetters }                    from 'vuex';

import TikTokAccountsMultipleTokensCheck from './TikTokAccountsMultipleTokensCheck.vue';

export default {
  name: 'TiktokAdsmanagerAccountsTopbar',

  components: {
    TikTokAccountsMultipleTokensCheck,
    CommonStatDate,
  },

  data() {
    return {
      nameSearchText: '',
      loading: false
    };
  },

  computed: {
    ...mapGetters({
      selected: 'tiktokAdsmanagerAccounts/selected',
    }),
  },

  methods: {
    async addAccount() {
      await this.$store.dispatch('tiktokAdsmanagerAccounts/setSingleAccountData');
      this.$store.dispatch('tiktokAdsmanagerAccounts/openDialog', 'create');
    },

    filterName(name) {
      this.nameSearchText = name;
      setTimeout(async () => {
        if (name === null || name.length === 0) {
          await this.$store.dispatch('tiktokAdsmanagerAccounts/loadMainTableData', {
            endpoint: 'tiktok/accounts'
          });
        } else if (name === this.nameSearchText) {
          await this.$store.dispatch('tiktokAdsmanagerAccounts/loadMainTableData', {
            endpoint: 'tiktok/accounts',
            query: name
          });
        }
      }, 500);
    },

    async sync() {
      try {
        let requiredTime = 10;
        let blockTime = moment().utc().subtract(requiredTime, 'minutes').format('yyyy-MM-DD HH:mm:ss');
        this.selected.map(account => {
          if (account.last_sync > blockTime) {
            throw new Error(this.$t('common.syncAfterFewMinutes', { name: account.name , requiredTime: requiredTime }));
          }
        });

        this.loading = true;

        const ids = this.selected.map(i => i.id);
        const response = await this.api.post('/tiktok/accounts/sync', { ids });

        if (response.status === 200) {
          this.$store.dispatch('main/alert', {
            color: 'success',
            message: this.$t('adsmanager.accounts.accountsHaveBeenPlacedInSynchronizationQueue')
          });
          this.selected.map(account => account.last_sync = moment().utc().format('yyyy-MM-DD HH:mm:ss'));
          this.loading = false;
        } else {
          throw new Error(this.$t('common.somethingWentWrong'));
        }
      } catch (error) {
        this.loading = false;
        this.$store.dispatch('main/alert', {
          color: 'error',
          message: error.message
        });
      }
    },
    
    async bulkDelete() {
      this.$store.dispatch('tiktokAdsmanagerAccounts/openDialog', 'bulkDeleteConfirm');
    }
  }
};
</script>
