const tiktokAccessTokenValidation = (value) => {
  try {
    const decodedBase64 = window.atob(value);
    const decodedJson = JSON.parse(decodedBase64);

    const {
      cookies,
      useragent,
      timezone,
      browser_name,
      browser_version,
      browser_platform,
      browser_language,
      screen_width,
      screen_height } = decodedJson;

    return !!cookies &&
    !!useragent &&
    !!timezone &&
    !!browser_name &&
    !!browser_version &&
    !!browser_platform &&
    !!browser_language &&
    !!screen_width &&
    !!screen_height;

  } catch (_) {
    return false;
  }
};

export {
  tiktokAccessTokenValidation
};