export default {
  methods: {
    hasValidationInvalid(target, property) {
      if (this.$validation) {
        const { [target]: data } = this.$validation;
        if (data && data[property]) {
          return data[property].$invalid;
        }
      }
      return false;
    },
    
    hasSpecificInvalid(target, property, subproperty) {
      if (this.$validation) {
        const { [target]: data } = this.$validation;
        if (data && data[property] && data[property][subproperty]) {
          return data[property][subproperty].$invalid;
        }
      }
      return false;
    },

    hasSpecificError(target, property, subproperty) {
      if (this.$validation) {
        const { [target]: data } = this.$validation;
        if (data && data[property] && typeof data[property][subproperty] !== 'undefined') {
          return data[property][subproperty];
        }
      }
      return true;
    },

    hasSingleValidationInvalid(target) {
      if (this.$validation) {
        const { [target]: data } = this.$validation;
        if (data) {
          return data.$invalid;
        }
      }
      return false;
    },
  }
};