<template>
  <td>
    <span
      class="span-link"
    >{{ item.name }}
    </span>
    <!-- ДАННЫЕ -->
    <div class="d-block ad_account_data">
      <div class="d-flex">
        <span
          v-if="!$vuetify.breakpoint.xs"
          title="Account"
          style="line-height: 14px;"
        >
          {{ $t('common.account') }}:
        </span>
        <span
          v-if="!$vuetify.breakpoint.xs"
          title="Account"
          style="line-height: 14px;"
        >
          {{ item.account.name }}
        </span>
      </div>
      <div class="d-flex">
        <span
          v-if="!$vuetify.breakpoint.xs"
          title="TikTok ID"
          style="line-height: 14px;"
        >
          {{ $t('common.id') }}:
        </span>
        <span
          v-if="!$vuetify.breakpoint.xs"
          title="TikTok ID"
          style="line-height: 14px;"
        >{{ item.tiktok_id }}</span>
        <v-icon
          size="10"
          style="padding-left: 5px"
          @click="copyToClipboard(item.tiktok_id)"
        >
          far fa-copy
        </v-icon>
      </div>

      <div v-if="item.pixels && item.pixels.length">
        <div
          v-for="pixel in item.pixels"
          :key="`TiktokAdsmanagerAdAccountsMainTablePixels-${pixel.pixel_code}`"
          style="line-height: 14px"
          class="d-flex"
        >
          <span>
            {{ $t('common.pixelId') + ': ' + pixel.pixel_code }}
          </span>
          <v-icon
            size="10"
            class="ml-1"
            @click="copyToClipboard(pixel.pixel_code)"
          >
            far fa-copy
          </v-icon>
        </div>
      </div>
    </div>
    <!-- ДАННЫЕ КОНЕЦ -->
  </td>
</template>
<script>
import clipboard from '@/mixins/clipboard';

export default {
  name: 'TiktokAdsmanagerAdAccountsMainTableName',

  mixins: [ clipboard ],

  props: {
    item: {
      type: Object,
      required: true
    },
  },

  methods: {
    async editAccount(data) {
      await this.$store.dispatch('tiktokAdsmanagerAccounts/setSingleAccountData', data);
      this.$store.dispatch('tiktokAdsmanagerAccounts/openDialog', 'edit');
    },
    copyToClipboard(value)
    {
      navigator.clipboard.writeText(value).then();
    }
  }
};
</script>

<style>
.ad_account_data {
  font-size: 11px;
  color: gray;
  min-width: 220px;
}
</style>