<template>
  <v-dialog
    :value="dialogs.bulkDuplicate"
    width="500"
    :transition="false"
    scrollable
    @click:outside="closeDialog"
    @keydown.esc="closeDialog"
  >
    <v-card>
      <v-toolbar
        :height="48"
        dark
        color="primary"
        class="mb-5"
        flat
      >
        <v-toolbar-title>
          {{ $t('common.duplicate') }}
        </v-toolbar-title>

        <v-spacer />

        <v-toolbar-items>
          <v-btn
            icon
            @click="closeDialog"
          >
            <v-icon>
              fas fa-times
            </v-icon>
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>

      <v-card-text>
        <v-text-field
          v-model="count"
          type="number"
          min="0"
          outlined
          dense
          hide-details="auto"
          :error-messages="countErrors"
          @blur="blurCount"
        />
      </v-card-text>

      <v-divider />

      <v-card-actions>
        <v-spacer />
        <v-btn
          text
          color="grey"
          @click="closeDialog"
        >
          {{ $t('common.close') }}
        </v-btn>
        <v-btn
          text
          color="primary"
          :loading="loading"
          @click="touch"
        >
          {{ $t('common.duplicate') }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { required, minValue, maxValue } from 'vuelidate/lib/validators';
import { mapGetters }                   from 'vuex';

export default {
  name: 'TiktokAdsmanagerAdsDialogsBulkDuplicate',

  data() {
    return {
      loading: false,
      count: 0,
      touched: false,
    };
  },

  validations: {
    count: { required, minValue: minValue(1), maxValue: maxValue(10) },
  },

  computed: {
    ...mapGetters({
      dialogs: 'tiktokAdsmanagerAds/dialogs',
      selected: 'tiktokAdsmanagerAds/selected',
    }),

    countErrors() {
      if (this.touched && (!this.$v.count.minValue || !this.$v.count.required)) {
        return this.$t('validation.min', { length: 1 }); 
      }
      if (this.touched && !this.$v.count.maxValue) {
        return this.$t('validation.max', { length: 10 }); 
      }
      return null;
    },
  },

  watch: {
    count() { this.touched = false; },
  },

  methods: {
    closeDialog() {
      this.$store.dispatch('tiktokAdsmanagerAds/closeDialog', 'bulkDuplicate');
    },

    touch() {
      this.$v.$touch();

      if (this.$v.$invalid) {
        this.touched = true;
      } else {
        this.bulkDuplicate();
      }
    },

    async bulkDuplicate() {
      try {
        this.loading = true;
    
        const ids = this.selected.map(i => i.id);
        const count = this.count;
        const response = await this.api.post('/tiktok/creatives/duplicate', { ids, count });

        if (response.status === 200) {
          await this.$store.dispatch('tiktokAdsmanagerAds/loadMainTableData', { endpoint: 'tiktok/creatives' });
          await this.$store.dispatch('main/alert', {
            color: 'success',
            message: this.$t('common.success')
          });
          this.loading = false;
          await this.$store.dispatch('tiktokAdsmanagerAds/clearSelected');
          this.closeDialog();
        } else {
          throw new Error('status not 200');
        }
      } catch (error) {
        this.loading = false;
        this.$store.dispatch('main/alert', {
          color: 'error',
          message: this.$t('common.somethingWentWrong')
        });
      }
    },

    blurCount() {
      if (!this.count || this.count < 0) {
        this.count = 0;
      }
    },
  }
};
</script>
