<template>
  <v-card>
    <v-card-text style="padding: 0px 8px 0px 8px">
      <!-- ТАБЛИЦА -->
      <v-data-table 
        v-model="selected"
        :items="mainTableData"
        :items-per-page="footerOptions.itemsPerPage"
        :headers="cols"
        fixed-header
        :height="innerHeight - 160"
        show-select
        :loading="mainTableLoading"
        hide-default-footer
        :custom-sort="customSortsTiktokAdAccounts"
        class="tiktok-data-table"
      >
        <template #[`header.data-table-select`]>
          <v-simple-checkbox
            :ripple="false"
            class="px-2"
            :value="!!selected.length && mainTableData.length === selected.length"
            :indeterminate="!!selected.length && mainTableData.length !== selected.length"
            @input="selectAll"
          />
        </template>
        
        <template #body="{ items }">
          <tbody>
            <tr
              v-for="item in items"
              :key="`TiktokAdsmanagerAccountsMainTableRow-${item.id}`"
            >
              <!-- ГАЛОЧКА -->
              <td>
                <v-simple-checkbox
                  :ripple="false"
                  class="px-2"
                  :value="selected.findIndex(i => i.id === item.id) > -1"
                  @input="selectItem(item, $event)"
                />
              </td>
              <!-- ГАЛОЧКА КОНЕЦ -->

              <!-- НАЗВАНИЕ -->
              <main-table-name :item="item" />
              <!-- НАЗВАНИЕ КОНЕЦ -->

              <!-- СТАТУС -->
              <main-table-status :item="item" />
              <!-- СТАТУС КОНЕЦ -->

              <!--  БАЛАНС  -->
              <main-table-balance :item="item" />
              <!--  БАЛАНС  КОНЕЦ -->

              <!-- СТАТИСТИКА -->
              <td
                v-for="aggregateColumn in aggregateColumns"
                :key="`TiktokAdsmanagerAccountsMainTableDataCell-${aggregateColumn}`"
                class="text-center"
              >  
                {{ statCellData(item, aggregateColumn) }}
              </td>
              <!-- СТАТИСТИКА КОНЕЦ -->
            </tr>
          </tbody>
        </template>

        <!-- ФУТЕР -->
        <template #footer>
          <main-table-footer
            :footer-options="footerOptions"
            :footer-pagination="footerPagination"
          />
        </template>
        <!-- ФУТЕР КОНЕЦ -->
      </v-data-table>
    </v-card-text>
  </v-card>
</template>

<script>
import customSort       from '@/mixins/adsmanager/custom_sort';
import footerDataMixin  from '@/mixins/tiktok/common/footer_data';
import mainTableMixin   from '@/mixins/tiktok/common/main_table';
import { mapFields }    from 'vuex-map-fields';
import { mapGetters }   from 'vuex';

import MainTableBalance from './TiktokAdsmanagerAdAccountsMainTableBalance.vue';
import MainTableFooter  from './TiktokAdsmanagerAdAccountsMainTableFooter.vue';
import MainTableName    from './TiktokAdsmanagerAdAccountsMainTableName.vue';
import MainTableStatus  from './TiktokAdsmanagerAdAccountsMainTableStatus.vue';

export default {
  name: 'TiktokAdsmanagerAdAccountsMainTable',

  components: {
    MainTableName,
    MainTableStatus,
    MainTableBalance,
    MainTableFooter,
  },

  mixins: [ footerDataMixin, customSort, mainTableMixin, ],

  computed: {
    ...mapGetters({
      innerHeight: 'main/innerHeight',
      mainTableData: 'tiktokAdsmanagerAdAccounts/mainTableData',
      mainTableLoading: 'tiktokAdsmanagerAdAccounts/mainTableLoading',
      mainTablePagination: 'tiktokAdsmanagerAdAccounts/mainTablePagination',
      aggregateColumns: 'tiktokAdsmanager/aggregateColumns',
    }),
    ...mapFields('tiktokAdsmanagerAdAccounts', ['selected']),

    cols() {
      const cols = [
        { text: this.$t('common.name'), value: 'name', sortable: true, width: 200},
        { text: this.$t('common.status'), value: 'status', sortable: true, width: 150},
        { text: this.$t('common.balance'), value: 'balance', sortable: true, width: 100},
      ];

      if (this.aggregateColumns) {
        for (const aggregateColumn of this.aggregateColumns) {
          cols.push({
            text: this.$t(`adsmanager.cols.tiktok.${aggregateColumn}`), value: `stat-${aggregateColumn}`, sortable: false, align: 'center', width: 150
          });
        }
      }

      return cols;
    },
  },
};
</script>

<style lang="scss">
.tiktok-data-table {
  thead {
    tr {
      th {
        &:nth-child(2) {
          min-width: 100px;
        }
      }
    }
  }

}
</style>
