<template>
  <div>
    <v-dialog
      v-model="checkTokenLoading.loading"
      hide-overlay
      persistent
      width="300"
    >
      <v-card
        color="primary"
        class="pr-4"
      >
        <v-card-text>
          <div style="display: flex; justify-content: space-between; align-items: center; min-height: 40px; color: white;">
            <div>
              {{ $t('dialogs.accounts.multipleTokensCheck.title') }}
            </div>
            <div>
              {{ checkTokenLoading.done }} / {{ checkTokenLoading.accounts.length }}
            </div>
          </div>

          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          />

          <v-btn
            icon
            style="position: absolute; right: 2px; top: 2px;"
            small
            @click="$store.dispatch('tiktokAdsmanagerAccounts/setLoadingOptions', {dialog: 'checkTokenLoading', option: 'stopRequest', value: true});"
          >
            <v-icon
              :size="14"
              color="white"
            >
              fas fa-times-circle
            </v-icon>
          </v-btn>
        </v-card-text>
      </v-card>
    </v-dialog>

    <v-tooltip bottom>
      <template v-slot:activator="{ on }">
        <v-btn
          small
          :disabled="selected.length === 0"
          color="primary"
          text
          style="min-width: 0; width: 30px; margin-top: 1px;"
          @click="multipleCheckTokens"
          v-on="on"
        >
          <v-icon :size="12">
            fas fa-code
          </v-icon>
        </v-btn>
      </template>
      <span>{{ $t('common.checkTokens') }}</span>
    </v-tooltip>

    <v-btn
      v-if="$vuetify.breakpoint.xs || $vuetify.breakpoint.sm"
      color="primary"
      text
      block
      small
      @click="multipleCheckTokens"
    >
      {{ $t('dialogs.accounts.multipleTokensCheck.title') }}
    </v-btn>
  </div>
</template>

<script>
import axios                     from 'axios';
import {mapGetters}              from 'vuex';

export default {
  name: 'TikTokAccountsMultipleTokensCheck',

  data() {
    return {
    };
  },

  computed: {
    ...mapGetters({
      selected: 'tiktokAdsmanagerAccounts/selected',
      checkTokenLoading: 'tiktokAdsmanagerAccounts/checkTokenLoading',
    })
  },

  watch: {
    'checkTokenLoading.stopRequest': {
      handler() {
        if (this.checkTokenLoading.stopRequest) {
          for (let request of this.checkTokenLoading.source) {
            request.cancel();
          }
          this.$store.dispatch('tiktokAdsmanagerAccounts/setLoadingOptions', {dialog: 'checkTokenLoading', option: 'showLoading', value: false});
        }
      }
    }
  },

  methods: {
    multipleCheckTokens() {
      if (this.checkTokenLoading.requestStatus) return;
      if (!confirm(this.$t('common.confirmPlease'))) return;
      this.$store.dispatch('tiktokAdsmanagerAccounts/setLoadingOptions', {dialog: 'checkTokenLoading', option: 'requestStatus', value: true});
      this.$store.dispatch('tiktokAdsmanagerAccounts/setLoadingOptions', {dialog: 'checkTokenLoading', option: 'stopRequest', value: false});
      this.$store.dispatch('tiktokAdsmanagerAccounts/setLoadingOptions', {dialog: 'checkTokenLoading', option: 'loading', value: true});
      this.$store.dispatch('tiktokAdsmanagerAccounts/setLoadingAccounts');

      if (this.selected && this.selected.length > 0) {
        this.$store.dispatch('tiktokAdsmanagerAccounts/setLoadingOptions', {dialog: 'checkTokenLoading', option: 'accounts', value: this.selected});
        this.checkTokenLoading.accounts.forEach((account, index) => {
          const CancelToken = axios.CancelToken;
          let sourceData = {
            dialog: 'checkTokenLoading',
            index: index,
            source: CancelToken.source(),
          };
          this.$store.dispatch('tiktokAdsmanagerAccounts/setLoadingSource', sourceData);
          this.api.get(`/tiktok/accounts/${account.id}/check-token/`, {
            cancelToken: this.checkTokenLoading.source[index].token
          }).catch(() => true)
            .then((response) => {
              this.$store.dispatch('tiktokAdsmanagerAccounts/setLoadingOptions', {dialog: 'checkTokenLoading', option: 'done', value: this.checkTokenLoading.done + 1});

              if (this.checkTokenLoading.done === this.checkTokenLoading.accounts.length) {
                this.$store.dispatch('tiktokAdsmanagerAccounts/setLoadingOptions', {dialog: 'checkTokenLoading', option: 'done', value: 0});
                this.$store.dispatch('tiktokAdsmanagerAccounts/setLoadingOptions', {dialog: 'checkTokenLoading', option: 'requestStatus', value: false});
                this.$store.dispatch('tiktokAdsmanagerAccounts/setLoadingOptions', {dialog: 'checkTokenLoading', option: 'loading', value: false});
                this.$store.dispatch('tiktokAdsmanagerAccounts/loadMainTableData', { endpoint: 'tiktok/accounts' });
              }
            });
        });
        this.$store.dispatch('tiktokAdsmanagerAccounts/clearSelected');
      }
    }
  }
};
</script>
<style scoped>
.v-dialog__content {
  align-items: flex-end !important;
  justify-content: flex-end !important;
}
</style>
