<template>
  <v-dialog
    :value="dialogs.create"
    persistent
    width="500"
    :transition="false"
    @keydown.esc="$store.dispatch('tiktokAdsmanagerAccounts/closeDialog', 'create')"
  >
    <v-card>
      <!-- ДИАЛОГИ -->
      <dialogs-how-to-get-token v-if="dialogs.howToGetToken" />
      <!-- ДИАЛОГИ КОНЕЦ -->

      <!-- ХЭДЕР ДИАЛОГА -->
      <dialog-header />
      <!-- ХЭДЕР ДИАЛОГА КОНЕЦ-->

      <v-card-text>
        <v-row>
          <!-- НАЗВАНИЕ АККАУНТА -->
          <v-col v-bind="colsProps">
            <v-text-field
              :value="singleAccountData.name"
              :label="$t('adsmanager.accounts.accountName')"
              outlined
              dense
              :error-messages="hasValidationInvalid('singleAccountData', 'name') ? $t('common.required') : null"
              hide-details="auto"
              :spellcheck="false"
              @input="setValue('name', $event)"
            />
          </v-col>
          <!-- НАЗВАНИЕ АККАУНТА КОНЕЦ -->

          <!-- ТОКЕН ДОСТУПА -->
          <v-col v-bind="colsProps">
            <!-- КАК ПОЛУЧИТЬ ТОКЕН -->
            <div
              class="cursor-pointer"
              @click="$store.dispatch('tiktokAdsmanagerAccounts/openDialog', 'howToGetToken')"
            >
              <v-icon
                size="16"
                class="mr-1"
                color="primary"
              >
                far fa-question-circle
              </v-icon>
              <span class="span-link">{{ $t('adsmanager.accounts.howToGetToken') }}</span>
            </div>
            <!-- КАК ПОЛУЧИТЬ ТОКЕН КОНЕЦ -->

            <v-textarea
              :value="singleAccountData.accessToken"
              :label="$t('common.access_token')"
              outlined
              dense
              :error="accessTokenError"
              :error-messages="accessTokenErrorMessage"
              hide-details="auto"
              :spellcheck="false"
              @input="setValue('accessToken', $event)"
            />
            <v-alert
              v-if="showAccessTokenAlert"
              text
              color="red"
              class="mb-0 mt-2"
              dense
            >
              {{ $t('validation.invalidTiktokAccessToken1') }}
              <a
                href="https://chrome.google.com/webstore/detail/dolphin-tt/ipjboigiejagkjgffdhcmdpkldmjfghe"
                target="_blank"
                class="span-link"
              >
                {{ $t('validation.invalidTiktokAccessToken2') }}
              </a>
            </v-alert>
          </v-col>
          <!-- ТОКЕН ДОСТУПА КОНЕЦ -->

          <!-- ПРОКСИ -->
          <v-col v-bind="colsProps">
            <v-autocomplete
              :value="singleAccountData.proxyId"
              :label="$t('common.proxy')"
              outlined
              dense
              hide-details
              :item-text="proxyName"
              :item-value="item => item.id"
              :items="proxyItems"
              @input="setProxy"
            />
          </v-col>
          <!-- ПРОКСИ КОНЕЦ -->
        </v-row>
      </v-card-text>

      <!-- ДЕЙСТВИЯ ДИАЛОГА -->
      <v-divider />
      <dialog-actions />
      <!-- ДЕЙСТВИЯ ДИАЛОГА КОНЕЦ-->
    </v-card>
  </v-dialog>
</template>
<script>
import validationMixin      from '@/mixins/tiktok/common/validation_mixin';
import { mapGetters }       from 'vuex';

import DialogsHowToGetToken from '../dialogs/TiktokAdsmanagerAccountsDialogsHowToGetToken.vue';

import DialogActions        from './TiktokAdsmanagerCreateDialogActions.vue';
import DialogHeader         from './TiktokAdsmanagerCreateDialogHeader.vue';

export default {
  name: 'TiktokAdsmanagerCreateDialog',

  components: {
    DialogHeader,
    DialogActions,
    DialogsHowToGetToken,
  },

  mixins: [ validationMixin ],

  data() {
    return {
      colsProps: {
        cols: '12',
      }
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'tiktokAdsmanagerAccounts/dialogs',
      singleAccountData: 'tiktokAdsmanagerAccounts/singleAccountData',
      $validation: 'tiktokAdsmanagerAccounts/$validation',
      proxy: 'proxy/proxy',
    }),

    accessTokenErrorMessage() {
      if (!this.hasSpecificError('singleAccountData', 'accessToken', 'required')) {
        return this.$t('common.required');
      }
      return null;
    },

    accessTokenError() {
      if (
        !this.hasSpecificError('singleAccountData', 'accessToken', 'required') ||
        !this.hasSpecificError('singleAccountData', 'accessToken', 'tiktokAccessTokenValidation')
      ) {
        return true;
      }
      return false;
    },

    showAccessTokenAlert() {
      if (
        this.hasSpecificError('singleAccountData', 'accessToken', 'required') &&
        !this.hasSpecificError('singleAccountData', 'accessToken', 'tiktokAccessTokenValidation')
      ) {
        return true;
      }
      return false;
    },

    proxyItems() {
      const items = [{
        name: this.$t('common.proxyNo'),
        id: null
      }];
      return items.concat(this.proxy.all);
    }
  },

  created() {
    this.getProxies();
  },

  destroyed() {
    this.$store.dispatch('tiktokAdsmanagerAccounts/clearSingleAccountData');
    this.$store.dispatch('tiktokAdsmanagerAccounts/setValidation', null);
  },

  methods: {
    async getProxies() {
      if (this.proxy.all.length === 0) {
        await this.$store.dispatch('proxy/loadProxy');
      }

      const proxyId = localStorage.getItem('dolphinTiktokAdsmanagerAccountsCreateProxyId');
      if (proxyId && proxyId !== 'null') {
        if (this.proxy.all.findIndex(item => item.id === proxyId) > -1) {
          this.setValue('proxyId', proxyId);
        }
      }
    },
    setValue(key, value) {
      this.$store.dispatch('tiktokAdsmanagerAccounts/setSingleAccountDataProperty', { key, value });
    },

    setProxy(value) {
      this.setValue('proxyId', value);
      localStorage.setItem('dolphinTiktokAdsmanagerAccountsCreateProxyId', value);
    },

    proxyName(proxy) {
      const { name, host, port, type, login, password } = proxy;
      if (name) {
        return name;
      } else {
        let proxyName = `${type}://${host}:${port}`;
        if (login && password) {
          proxyName += `@${login}:${password}`;
        }
        return proxyName;
      }
    }
  }
};
</script>