export default {
  computed: {
    footerOptions() {
      const options = {
        page: 1,
        itemsPerPage: 1
      };

      if (this.mainTablePagination) {
        const { current_page, per_page } = this.mainTablePagination;
        options.page = this.notEmpty(current_page) ? parseInt(current_page, 10) : 1,
        options.itemsPerPage = this.notEmpty(per_page) ? parseInt(per_page, 10) : 10;
      }

      return options;
    },

    footerPagination () {
      let pagination = {};

      if (this.mainTablePagination) {
        const { 
          current_page,
          per_page,
          from,
          to,
          last_page,
          total } = this.mainTablePagination;
        pagination = {
          page: this.notEmpty(current_page) ? parseInt(current_page, 10) : 1,
          itemsPerPage: this.notEmpty(per_page) ? parseInt(per_page, 10) : 10,
          pageStart: this.notEmpty(from) ? parseInt(from, 10) - 1 : 0,
          pageStop: this.notEmpty(to) ? parseInt(to, 10) : 1,
          pageCount: this.notEmpty(last_page) ? parseInt(last_page, 10) : 1,
          itemsLength: this.notEmpty(total) ? parseInt(total, 10) : 0
        };
      }

      return pagination;
    },
  },
  
  methods: {
    notEmpty(val) {
      return val !== undefined && val !== null;
    },
  }
};