<template>
  <v-card-actions>
    <v-spacer />
    <v-btn
      text
      color="grey"
      @click="closeDialog"
    >
      {{ $t('common.close') }}
    </v-btn>
    <v-btn
      text
      color="primary"
      :loading="loading"
      @click="touch"
    >
      {{ $t('common.add') }}
    </v-btn>
  </v-card-actions>
</template>

<script>
import { tiktokAccessTokenValidation } from '@/helpers/validation';
import { required }                    from 'vuelidate/lib/validators';
import { mapGetters }                  from 'vuex';

export default {
  name: 'TiktokAdsmanagerCreateDialogActions',

  data() {
    return {
      loading: false
    };
  },

  validations () {
    const singleAccountData = {
      name: { required },
      accessToken: { required, tiktokAccessTokenValidation }
    };

    return { singleAccountData };
  },

  computed: {
    ...mapGetters({
      singleAccountData: 'tiktokAdsmanagerAccounts/singleAccountData',
      $validation: 'tiktokAdsmanagerAccounts/$validation'
    }),
  },

  watch: {
    singleAccountData: {
      deep: true,
      handler() {
        // ПРИ ИЗМЕНЕНИИ ЛЮБОГО СВОЙСТВА СБРАСЫВАТЬ ВАЛИДАЦИЮ
        if (this.$validation) {
          this.$store.dispatch('tiktokAdsmanagerAccounts/setValidation', null);
        }
      }
    },
  },

  methods: {
    closeDialog() {
      this.$store.dispatch('tiktokAdsmanagerAccounts/closeDialog', 'create');
    },

    touch() {
      this.$v.$touch();

      if (this.$v.$invalid) {
        this.$store.dispatch('tiktokAdsmanagerAccounts/setValidation', this.$v);
      } else {
        this.add();
      }
    },

    async add() {
      try {
        this.loading = true;
        const data = {
          name: this.singleAccountData.name,
          access_token: this.singleAccountData.accessToken,
          proxy_id: this.singleAccountData.proxyId
        };

        const response = await this.api.post('/tiktok/accounts', data);
        if (response.status === 201) {
          this.$store.dispatch('main/alert', {
            color: 'success',
            message: this.$t('common.success')
          });

          this.$store.dispatch('tiktokAdsmanagerAccounts/loadMainTableData', { endpoint: 'tiktok/accounts' });
          this.closeDialog();
        } else {
          throw new Error('status not 201');
        }
      } catch (error) {
        this.loading = false;
        this.$store.dispatch('main/apiError',JSON.stringify(error.response.data.errors));
      }
    }
  }
};
</script>
