<template>
  <v-dialog
    :value="dialogs.bulkDeleteConfirm"
    width="500"
    :transition="false"
    scrollable
    @click:outside="closeDialog"
    @keydown.esc="closeDialog"
  >
    <v-card>
      <v-toolbar
        :height="48"
        dark
        color="primary"
        class="mb-5"
        flat
      >
        <v-toolbar-title>
          {{ $tc('adsmanager.accounts.deleteNAccounts', selected.length, { length: selected.length }) }}
        </v-toolbar-title>

        <v-spacer />

        <v-toolbar-items>
          <v-btn
            icon
            @click="closeDialog"
          >
            <v-icon>
              fas fa-times
            </v-icon>
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>

      <v-card-text>
        <v-data-table
          :headers="headers"
          hide-default-header
          hide-default-footer
          :items="selected"
          :items-per-page="-1"
        >
          <template #[`item.name`]="{item}">
            <span class="px-2">
              {{ item.name }}
            </span>
          </template>
        </v-data-table>
      </v-card-text>

      <v-divider />

      <v-card-actions>
        <v-spacer />
        <v-btn
          text
          color="grey"
          @click="closeDialog"
        >
          {{ $t('common.close') }}
        </v-btn>
        <v-btn
          text
          color="red"
          :loading="loading"
          @click="bulkDelete"
        >
          {{ $t('common.delete') }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'TiktokAdsmanagerAccountsDialogsBulkDeleteConfirm',

  data() {
    return {
      loading: false
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'tiktokAdsmanagerAccounts/dialogs',
      selected: 'tiktokAdsmanagerAccounts/selected',
    }),

    headers() {
      return [
        { text: this.$t('common.name'), value: 'name', sortable: false }
      ];
    },
  },

  methods: {
    closeDialog() {
      this.$store.dispatch('tiktokAdsmanagerAccounts/closeDialog', 'bulkDeleteConfirm');
    },

    async bulkDelete() {
      try {
        this.loading = true;
    
        const ids = this.selected.map(i => i.id);
        const response = await this.api.delete('/tiktok/accounts', { params: { ids } });

        if (response.status === 200) {
          await this.$store.dispatch('tiktokAdsmanagerAccounts/loadMainTableData', { endpoint: 'tiktok/accounts' });
          await this.$store.dispatch('main/alert', {
            color: 'success',
            message: this.$t('dialogs.accounts.delete.success')
          });
          this.loading = false;
          await this.$store.dispatch('tiktokAdsmanagerAccounts/clearSelected');
          this.closeDialog();
        } else {
          throw new Error('status not 200');
        }
      } catch (error) {
        this.loading = false;
        this.$store.dispatch('main/alert', {
          color: 'error',
          message: this.$t('common.somethingWentWrong')
        });
      }
    }
  }
};
</script>
