<template>
  <td>
    <v-chip
      :color="getColor"
      small
      class="ml-1"
      label
      outlined
    >
      {{ status }}
    </v-chip>
  </td>
</template>
<script>
import getStatusChipColor from '@/helpers/tiktok/mainTable/status';

export default {
  name: 'TiktokAdsmanagerAccountsMainTableStatus',

  props: {
    item: {
      type: Object,
      required: true
    },
  },

  data() {
    return {
      getStatusChipColor
    };
  },

  computed: {
    status() {
      if (this.item.status === 'ACTIVE' && this.item.api_token) {
        return this.item.status;
      }

      if (this.item.status === 'TOKEN_ERROR') {
        return this.item.status;
      }

      if (this.item.status === 'NEW') {
        return this.item.status;
      }

      return 'NEW';
    },

    getColor() {
      if (this.status === 'TOKEN_ERROR') {
        return 'red';
      }

      if (this.status === 'ACTIVE') {
        return 'green';
      }

      return 'orange';
    }
  },
};
</script>
