<template>
  <div class="d-flex align-center justify-space-between mb-2">
    <!-- ЛЕВАЯ ЧАСТЬ -->
    <div>
      <!-- КНОПКА ЗАЛИТЬ СВЯЗКУ -->
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            color="primary"
            :disabled="selected.length === 0"
            text
            icon
            v-on="on"
            @click="$router.push({path: `/tiktok/create-ads/wizard?adAccounts=${selectedAdAccountsIds}`, })"
          >
            <v-icon :size="14">
              fas fa-cloud-upload-alt
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.createAd') }}</span>
      </v-tooltip>
      <!-- КНОПКА ЗАЛИТЬ СВЯЗКУ КОНЕЦ -->
      
      <v-menu
        offset-y
        open-on-hover
        close-on-content-click
      >
        <!-- ВЕРТИКАЛЬНОЕ ТРОЕТОЧИЕ -->
        <template v-slot:activator="{ on }">
          <v-btn
            :disabled="selected.length === 0"
            small
            color="primary"
            icon
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-ellipsis-v
            </v-icon>
          </v-btn>
        </template>
        <!-- ВЕРТИКАЛЬНОЕ ТРОЕТОЧИЕ КОНЕЦ -->

        <v-list>
          <!-- СОЗДАТЬ ПИКСЕЛЬ -->
          <v-list-item
            dense
            @click="createPixel"
          >
            <v-list-item-title>{{ $t('adsmanager.cabs.actions.createPixel') }}</v-list-item-title>
          </v-list-item>
          <!-- СОЗДАТЬ ПИКСЕЛЬ КОНЕЦ -->
        </v-list>
      </v-menu>
    </div>
    <!-- ЛЕВАЯ ЧАСТЬ КОНЕЦ -->

    <!-- ПРАВАЯ ЧАСТЬ -->
    <div class="d-flex align-center ">
      <!-- ДАТЫ СТАТИСТИКИ -->
      <common-stat-date />
      <!-- ДАТЫ СТАТИСТИКИ КОНЕЦ -->

      <!-- ПОИСК ПО НАЗВАНИЮ -->
      <v-text-field
        dense
        clearable
        outlined
        :label="$t('filters.searchByNameOrID')"
        single-line
        prepend-inner-icon="fas fa-search"
        hide-details
        :value="searchText"
        @input="filterName"
      />
    </div>
    <!-- ПРАВАЯ ЧАСТЬ КОНЕЦ -->
  </div>
</template>

<script>
import CommonStatDate from '@/components/tiktok/adsmanager/common/TiktokAdsmanagerCommonStatDate.vue';
import { mapGetters } from 'vuex';

export default {
  name: 'TiktokAdsmanagerAdAccountsTopbar',

  components: {
    CommonStatDate,
  },

  data() {
    return {
      searchText: '',
      loading: false
    };
  },

  computed: {
    ...mapGetters({
      selected: 'tiktokAdsmanagerAdAccounts/selected',
    }),

    selectedAdAccountsIds() {
      return this.selected.map(i => i.id).join();
    }
  },

  methods: {
    createPixel() {
      this.$store.dispatch('tiktokAdsmanagerAdAccounts/openDialog', 'createPixel');
    },

    filterName(search) {
      this.searchText = search;
      setTimeout(async () => {
        if (search === this.searchText) {
          await this.$store.dispatch('tiktokAdsmanagerAdAccounts/loadMainTableData', {
            endpoint: 'tiktok/adaccounts',
            query: search
          });
        }
      }, 500);
    },
  }
};
</script>
