<template>
  <v-dialog
    :value="dialogs.bulkChangeBudget"
    width="800"
    :transition="false"
    scrollable
    @click:outside="closeDialog"
    @keydown.esc="closeDialog"
  >
    <v-card>
      <v-toolbar
        :height="48"
        dark
        color="primary"
        class="mb-5"
        flat
      >
        <v-toolbar-title>
          {{ $t('common.budgetAndBid') }}
        </v-toolbar-title>

        <v-spacer />

        <v-toolbar-items>
          <v-btn
            icon
            @click="closeDialog"
          >
            <v-icon>
              fas fa-times
            </v-icon>
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>

      <v-card-text>
        <v-data-table
          :headers="headers"
          fixed-header
          hide-default-footer
          :items="items"
          :items-per-page="-1"
        >
          <template #[`item.name`]="{item}">
            <div class="pr-2">
              {{ item.name }}
            </div>
          </template>

          <template #[`item.budget`]="{item}">
            <div class="pr-2">
              <span v-if="item.campaignBudget">-</span>
              <v-text-field
                v-else
                v-model="item.budget"
                type="number"
                min="0"
                outlined
                dense
                hide-details="auto"
                :label="$t('common.budgetDaily')"
                @blur="handleBlur(item, 'budget')"
              />
            </div>
          </template>

          <template #[`item.bidSize`]="{item}">
            <div class="pr-2">
              <span v-if="item.campaignBudget">-</span>
              <v-text-field
                v-else
                v-model="item.bid"
                type="number"
                min="0"
                outlined
                dense
                hide-details="auto"
                :label="$t('common.bidSize')"
                @blur="handleBlur(item, 'bid')"
              />
            </div>
          </template>
        </v-data-table>
      </v-card-text>

      <v-divider />

      <v-card-actions>
        <v-spacer />
        <v-btn
          text
          color="grey"
          @click="closeDialog"
        >
          {{ $t('common.close') }}
        </v-btn>
        <v-btn
          text
          color="primary"
          :loading="loading"
          @click="bulkChangeBudget"
        >
          {{ $t('common.update') }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import axios          from 'axios';
import { mapGetters } from 'vuex';

export default {
  name: 'TiktokAdsmanagerAdsetsDialogsBulkChangeBudget',

  data() {
    return {
      loading: false,
      items: []
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'tiktokAdsmanagerAdsets/dialogs',
      selected: 'tiktokAdsmanagerAdsets/selected',
      innerHeight: 'main/innerHeight',
    }),

    headers() {
      return [
        { text: this.$t('common.name'), value: 'name', sortable: false },
        { text: this.$t('common.budget'), value: 'budget', sortable: false, width: 200 },
        { text: this.$t('common.bidSize'), value: 'bidSize', sortable: false, width: 200 },
      ];
    },
  },

  watch: {
    items: {
      deep: true,
      handler() {
        console.log('asdads');
      }
    },
  },

  created() {
    for (const item of this.selected) {
      this.$set(this.items, this.items.length, {
        name: item.ad_name,
        ids: [item.id],
        bid: parseInt(item.bid, 10) ? parseInt(item.bid, 10) : 0,
        budget: parseInt(item.budget, 10) ? parseInt(item.budget, 10) : 0,
        campaignBudget: item.budget===0,
      });
    }
  },

  methods: {
    closeDialog() {
      this.$store.dispatch('tiktokAdsmanagerAdsets/closeDialog', 'bulkChangeBudget');
    },

    async bulkChangeBudget() {
      try {
        const items = this.items.filter(i => !i.campaignBudget);

        await axios.all(items.map(({ bid, budget, ids}) => this.api.patch('/tiktok/adsets/budget-change', { bid, budget, ids})));
        this.$store.dispatch('tiktokAdsmanagerAdsets/loadMainTableData', { endpoint: 'tiktok/adsets' });

        this.$store.dispatch('main/alert', {
          color: 'success',
          message: this.$t('common.success')
        });
        this.closeDialog();
      } catch (error) {
        this.$store.dispatch('main/alert', {
          color: 'error',
          message: this.$t('common.somethingWentWrong')
        });
      }
    },

    handleBlur(item, type) {
      if (type === 'budget') {
        if (!item.budget || item.budget < 0) {
          item.budget = 0;
        }
      }
      if (type === 'bid') {
        if (!item.bid || item.bid < 0) {
          item.bid = 0;
        }
      }
    },
  }
};
</script>
