<template>
  <td>
    <v-chip
      v-if="item.status === 4"
      style="width: fit-content"
      :color="getStatusChipColor(item.status)"
      small
      class="ml-1"
      label
      outlined
    >
      ACTIVE
    </v-chip>
    <v-chip
      v-else-if="item.status === 8"
      style="width: fit-content"
      :color="getStatusChipColor(item.status)"
      small
      class="ml-1"
      label
      outlined
    >
      PENALIZED
    </v-chip>
    <v-chip
      v-else
      :color="getStatusChipColor(item.status)"
      small
      class="ml-1"
      label
      outlined
    >
      {{ item.status }}
    </v-chip>
  </td>
</template>
<script>
export default {
  name: 'TiktokAdsmanagerAdAccountsMainTableStatus',

  props: {
    item: {
      type: Object,
      required: true
    },
  },

  methods: {
    getStatusChipColor(status) {
      switch (status) {
      case 4 : return 'success';
      case 6 : return 'orange';
      case 8 : return 'red';
      default : return 'orange';
      }
    },
  }
};
</script>