<template>
  <v-tabs
    centered
    grow
    :value="tabNumber"
    :height="32"
  >
    <!-- АККАУНТЫ -->
    <v-tab
      key="tab-accounts"
      :value="0"
      @click="selectTab('accounts')"
    >
      <tabs-badge
        tab-name="accounts"
        :current-tab-number="tabNumber"
        :selected="selectedAccounts"
      />
    </v-tab>
    <!-- АККАУНТЫ КОНЕЦ -->

    <!-- КАБЫ -->
    <v-tab
      key="tab-ad-accounts"
      :value="1"
      @click="selectTab('ad-accounts')"
    >
      <tabs-badge
        tab-name="ad-accounts"
        :current-tab-number="tabNumber"
        :selected="selectedAdAccounts"
      />
    </v-tab>
    <!-- КАБЫ КОНЕЦ -->

    <!-- КАМПАНИИ -->
    <v-tab
      key="tab-campaigns"
      :value="2"
      @click="selectTab('campaigns')"
    >
      <tabs-badge
        tab-name="campaigns"
        :current-tab-number="tabNumber"
        :selected="selectedCampaigns"
      />
    </v-tab>
    <!-- КАМПАНИИ КОНЕЦ -->

    <!-- АДСЕТЫ -->
    <v-tab
      key="tab-adsets"
      :value="3"
      @click="selectTab('adsets')"
    >
      <tabs-badge
        tab-name="adsets"
        :current-tab-number="tabNumber"
        :selected="selectedAdsets"
      />
    </v-tab>
    <!-- АДСЕТЫ КОНЕЦ -->

    <!-- ОБЪЯВЛЕНИЯ -->
    <v-tab
      key="tab-ads"
      :value="4"
      @click="selectTab('ads')"
    >
      <tabs-badge
        tab-name="ads"
        :current-tab-number="tabNumber"
        :selected="selectedAds"
      />
    </v-tab>
    <!-- ОБЪЯВЛЕНИЯ КОНЕЦ -->
  </v-tabs>
</template>

<script>
import { mapGetters } from 'vuex';

import TabsBadge      from './tabs/TiktokAdsmanagerTabsBadge.vue';

export default {
  name: 'TiktokAdsmanagerTabs',

  components: {
    TabsBadge
  },

  computed: {
    ...mapGetters({
      selectedAccounts: 'tiktokAdsmanagerAccounts/selected',
      selectedAdAccounts: 'tiktokAdsmanagerAdAccounts/selected',
      selectedCampaigns: 'tiktokAdsmanagerCampaigns/selected',
      selectedAdsets: 'tiktokAdsmanagerAdsets/selected',
      selectedAds: 'tiktokAdsmanagerAds/selected',
    }),

    tabNumber() {
      switch (this.$route.params.tab) {
      case 'accounts':
        return 0;
      case 'ad-accounts':
        return 1;
      case 'campaigns':
        return 2;
      case 'adsets':
        return 3;
      case 'ads':
        return 4;
      default:
        return 0;
      }
    }
  },
  
  watch: {
    selectedAccounts(newVal) {
      if (Array.isArray(newVal) && newVal.length) {
        if (this.selectedAdAccounts.length) {
          this.$store.dispatch('tiktokAdsmanagerAdAccounts/clearSelected');
        }
        if (this.selectedCampaigns.length) {
          this.$store.dispatch('tiktokAdsmanagerCampaigns/clearSelected');
        }
        if (this.selectedAdsets.length) {
          this.$store.dispatch('tiktokAdsmanagerAdsets/clearSelected');
        }
        if (this.selectedAds.length) {
          this.$store.dispatch('tiktokAdsmanagerAds/clearSelected');
        }
      }
    },
    selectedAdAccounts(newVal) {
      if (Array.isArray(newVal) && newVal.length) {
        if (this.selectedCampaigns.length) {
          this.$store.dispatch('tiktokAdsmanagerCampaigns/clearSelected');
        }
        if (this.selectedAdsets.length) {
          this.$store.dispatch('tiktokAdsmanagerAdsets/clearSelected');
        }
        if (this.selectedAds.length) {
          this.$store.dispatch('tiktokAdsmanagerAds/clearSelected');
        }
      }
    },
    selectedCampaigns(newVal) {
      if (Array.isArray(newVal) && newVal.length) {
        if (this.selectedAdsets.length) {
          this.$store.dispatch('tiktokAdsmanagerAdsets/clearSelected');
        }
        if (this.selectedAds.length) {
          this.$store.dispatch('tiktokAdsmanagerAds/clearSelected');
        }
      }
    },
    selectedAdsets(newVal) {
      if (Array.isArray(newVal) && newVal.length) {
        if (this.selectedAds.length) {
          this.$store.dispatch('tiktokAdsmanagerAds/clearSelected');
        }
      }
    }
  },

  methods: {
    selectTab(tab) {
      if (this.$route.params.tab !== tab) {
        this.$router.push({path: `/tiktok/adsmanager/${tab}`, params: { tab }});
      }
    },

  }
};
</script>