<template>
  <v-data-footer
    show-first-last-page
    :options="footerOptions"
    :pagination="footerPagination"
    :items-per-page-options="[10, 30, 50, 100]"
    @update:options="updateFooterOptions"
  />
</template>
<script>
export default {
  name: 'TiktokAdsmanagerCampaignsMainTableFooter',

  props: {
    footerOptions: {
      type: Object,
      required: true
    },
    footerPagination: {
      type: Object,
      required: true
    },
  },

  methods: {
    updateFooterOptions({ page, itemsPerPage }) {
      this.$store.dispatch('tiktokAdsmanagerCampaigns/loadMainTableData', {
        endpoint: 'tiktok/campaigns',
        page,
        itemsPerPage
      });
    },
  }
};
</script>